:root {
  --col0: #2C2B30;
  --col1: #FFE5Ec; 
  --col2: #F8B7CD; 
  --col3: #C8E7F5; 
  --col4: #67A3D9; 
  --col5: #EBECF0;
  --col6: #5e5457;
}

.app { width:100%; height:100%; }
* { box-sizing:border-box; margin:0; padding:0; font-family:'Lato',sans-serif }
body { background-color:var(--col0); min-height:100vh; padding-top:8vmin; }